import React from 'react';
import { forwardRefWrapper } from '../../libs/forward-ref-wrapper';
import { IntrinsicElementProps } from '../../types/utility';

interface SpacerProps extends IntrinsicElementProps<'div'> {
  size?: string | number;
  orientation?: 'horizontal' | 'vertical';
  inline?: boolean;
}

export const Spacer = /* @__PURE__ */ forwardRefWrapper<HTMLDivElement, SpacerProps>(
  'Spacer',

  {
    size: 0,
    orientation: 'horizontal',
  },

  (props, externalRef) => {
    const { size, orientation, inline, style: otherStyle, ...otherProps } = props;

    const horizontalStyle = { display: inline ? 'inline-block' : 'block', height: '100%', width: size };
    const verticalStyle = { display: inline ? 'inline-block' : 'block', width: '100%', height: size };
    const style = orientation === 'vertical' ? verticalStyle : horizontalStyle;

    return <div aria-hidden="true" style={{ ...style, ...otherStyle }} {...otherProps} ref={externalRef} />;
  },
);
