import { useCallback, useMemo } from 'react';
import { AnimationProps, TargetAndTransition, MotionAdvancedProps } from 'framer-motion';
import { useReducedMotion } from './use-reduced-motion';

type FramerVariants<Custom = void, VariantKeys extends string = string> = {
  [P in VariantKeys]: TargetAndTransition | ((custom: Custom) => TargetAndTransition);
};

type OptionalFramerVariants<Custom = void, VariantKeys extends string = string> = {
  [P in VariantKeys]?: TargetAndTransition | ((custom: Custom) => TargetAndTransition);
};

/**
 * Creates a custom animation hook for `framer-motion`.
 */
export function createFramerTransition<TCustom = void>() {
  const createHook = <VariantKeys extends string = string>(
    variants: FramerVariants<TCustom, VariantKeys>,
    reducedMotionVariants?: OptionalFramerVariants<TCustom, VariantKeys>,
  ) => (targets: {
    initial?: VariantKeys;
    animate?: VariantKeys;
    exit?: VariantKeys;
  }): ((custom?: TCustom) => AnimationProps & MotionAdvancedProps) => {
    const { initial, animate, exit } = targets;

    const prefersReducedMotion = useReducedMotion();
    const variantsResolved = useMemo(
      () => (prefersReducedMotion ? { ...variants, ...reducedMotionVariants } : { ...variants }),
      [prefersReducedMotion],
    );

    const createFramerProps = useCallback(
      (custom = {} as any) => ({ custom, variants: variantsResolved, initial, animate, exit }),
      [initial, animate, exit, variantsResolved],
    );

    return createFramerProps;
  };

  const withVariants = <VariantKeys extends string = string>(variants: FramerVariants<TCustom, VariantKeys>) => {
    const withReducedMotion = (reducedMotionVariants: OptionalFramerVariants<TCustom, VariantKeys>) => {
      return { use: createHook(variants, reducedMotionVariants) };
    };

    return { withReducedMotion, use: createHook(variants) };
  };

  return { withVariants };
}

// --- Out-of-the-box animations -------------------------------------------- //

const fadeTransition = /* @__PURE__ */ createFramerTransition().withVariants({
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
});

function useFade() {
  return fadeTransition.use({
    initial: 'hidden',
    animate: 'visible',
    exit: 'hidden',
  });
}

const scaleTransition = /* @__PURE__ */ createFramerTransition<number>()
  .withVariants({
    hiddenWithScale: (scale) => ({ opacity: 0, scale, transition: { duration: 0.1 } }),
    hiddenWithoutScale: { opacity: 0, scale: 1, transition: { duration: 0.1 } },
    visible: { opacity: 1, scale: 1, transition: { duration: 0.1 } },
  })
  .withReducedMotion({
    hiddenWithScale: { opacity: 0, scale: 1, transition: { duration: 0.1 } },
    hiddenWithoutScale: { opacity: 0, scale: 1, transition: { duration: 0.1 } },
  });

function useScale() {
  return scaleTransition.use({
    initial: 'hiddenWithScale',
    animate: 'visible',
    exit: 'hiddenWithScale',
  });
}

function useScaleIn() {
  return scaleTransition.use({
    initial: 'hiddenWithScale',
    animate: 'visible',
    exit: 'hiddenWithoutScale',
  });
}

function useScaleOut() {
  return scaleTransition.use({
    initial: 'hiddenWithoutScale',
    animate: 'visible',
    exit: 'hiddenWithScale',
  });
}

const slideTransition = /* @__PURE__ */ createFramerTransition<{
  x?: number;
  y?: number;
  scale?: number;
}>()
  .withVariants({
    hidden: ({ x = 0, y = 0, scale = 1 }) => ({
      x,
      y,
      scale,
      opacity: 0,
      transition: { ease: 'easeInOut', duration: 0.15 },
    }),
    visible: { x: 0, y: 0, opacity: 1, scale: 1, transition: { ease: 'easeInOut', duration: 0.15 } },
  })
  .withReducedMotion({
    hidden: {
      x: 0,
      y: 0,
      scale: 1,
      opacity: 0,
      transition: { ease: 'easeInOut', duration: 0.15 },
    },
  });

function useSlide() {
  return slideTransition.use({
    initial: 'hidden',
    animate: 'visible',
    exit: 'hidden',
  });
}

export const transitions = {
  useFade,
  useSlide,
  useScale,
  useScaleIn,
  useScaleOut,
};
